import { Component, OnInit } from '@angular/core';
import { WeatherService} from './weather.service';
import * as L from 'leaflet';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent {
  constructor(private userService: WeatherService) {}
  
  city: string;
    profile = {};
    icon;
    
 
  loadCity() {
      this.userService.searchWeatherInfo(this.city);
      this.userService.data().subscribe(data => this.profile = data);
      this.userService.data().subscribe(data => this.icon = "https://openweathermap.org/img/w/" + data.weather[0].icon + ".png");
         
    }
      
}



